import { useState, useEffect } from "react";
import { Link, useOutletContext, useNavigate, NavLink } from "react-router-dom";
import { useForm } from "react-hook-form";
import axios from "axios";
import { useDispatch } from "react-redux";
import { createAsyncMessage } from "../../slice/messageSlice";
import { formatCurrency } from "../../utilities/utils";
import { Input, Textarea, CheckboxRadio } from "../../components/FormElements";
import Loading from "../../components/Loading";

const Checkout = () => {
  const { cartData, getCart } = useOutletContext();
  const {
    register,
    handleSubmit,
    watch,
    getValues,
    control,
    reset,
    formState: { errors, isSubmitSuccessful },
  } = useForm({
    mode: "onTouched",
    defaultValues: {
      name: "",
      email: "",
      tel: "",
      address: "",
      message: "",
      payment: "creditCard",
      cardHolder: "",
      creditNum: null,
      creditCVV: null,
      creditExpDate: "",
      bankAccount: null,
    },
  });

  const watchPayment = watch("payment");

  const dispatch = useDispatch();

  const [submittedData, setSubmittedData] = useState({});
  const [couponCode, setCouponCode] = useState("");
  const [currentCoupon, setCurrentCoupon] = useState("");
  const [discountedTotal, setDiscountedTotal] = useState(0);
  const [isLoading, setIsLoading] = useState(false);

  const navigate = useNavigate();

  useEffect(() => {
    setIsLoading(true);
    getCart();
    setIsLoading(false);
  }, []);

  // Get the discountedTotal and currentCoupon from updated cartData
  useEffect(() => {
    setDiscountedTotal(cartData ? cartData.final_total : 0);

    if (cartData && cartData.carts && cartData.carts.length) {
      setCurrentCoupon(cartData?.carts[0]?.coupon?.code);
    } else {
      setCurrentCoupon("");
    }
  }, [cartData]);

  // Reset form and update cart num after form submission
  useEffect(() => {
    if (isSubmitSuccessful) {
      reset({
        name: "",
        email: "",
        tel: "",
        address: "",
        message: "",
        payment: "creditCard",
        cardHolder: "",
        creditNum: null,
        creditCVV: null,
        creditExpDate: "",
        bankAccount: null,
      });
    }
    getCart();
  }, [isSubmitSuccessful, submittedData, reset]);

  const handleCouponChange = (e) => {
    setCouponCode(e.target.value);
  };

  const handleApplyCoupon = async (e) => {
    setIsLoading(true);

    const data = {
      data: {
        code: couponCode,
      },
    };

    await axios
      .post(`/v2/api/${process.env.REACT_APP_API_PATH}/coupon`, data)
      .then((res) => {
        setDiscountedTotal(res.data.data.final_total);

        setCouponCode("");
        setIsLoading(false);
        dispatch(createAsyncMessage(res.data));
        getCart();
      })
      .catch((error) => {
        console.log(error);
        setIsLoading(false);
        dispatch(createAsyncMessage(error.response.data));
      });
  };

  const onSubmit = async (data) => {
    setIsLoading(true);

    const { name, email, tel, address, message } = data;

    const form = {
      data: {
        user: {
          name,
          email,
          tel,
          address,
        },
        message: message,
        status: "0",
      },
    };

    await axios
      .post(`/v2/api/${process.env.REACT_APP_API_PATH}/order`, form)
      .then((res) => {
        setCouponCode("");
        setSubmittedData(data);
        completePayment(res.data.orderId);
      })
      .catch((error) => {
        console.log(error);
        setIsLoading(false);
        dispatch(createAsyncMessage(error.response.data));
      });
  };

  const completePayment = async (orderId) => {
    await axios
      .post(`/v2/api/${process.env.REACT_APP_API_PATH}/pay/${orderId}`)
      .then((res) => {
        setIsLoading(false);
        dispatch(createAsyncMessage(res.data));
        navigate(`/success/${orderId}`);
      })
      .catch((error) => {
        console.log(error);
        setIsLoading(false);
        dispatch(createAsyncMessage(error.response.data));
      });
  };

  return (
    <div className="bg-white">
      <Loading isLoading={isLoading} />
      <section className="container">
        <nav aria-label="breadcrumb p-0">
          <ol className="breadcrumb m-0 py-2 lh-md border-bottom">
            <li className="breadcrumb-item">
              <NavLink className="text-decoration-none link-dark" to="/">
                <i className="bi bi-house-fill"></i>
              </NavLink>
            </li>
            <li className="breadcrumb-item">
              <NavLink className="text-decoration-none link-dark" to="/cart">
                
              </NavLink>
            </li>
            <li className="breadcrumb-item active" aria-current="page">
              
            </li>
          </ol>
        </nav>
      </section>
      <div className="container">
        <div className="row g-0 flex-column-reverse flex-md-row justify-content-center align-content-stretch">
          <form
            className="col col-md-7 px-5 pt-4 pb-5"
            onSubmit={handleSubmit(onSubmit)}
          >
            <div className="mb-5">
              <h2 className="h4 mb-4 text-dark"></h2>
              <div>
                <div className="mb-3">
                  <Input
                    id="name"
                    type="text"
                    errors={errors}
                    labelText=""
                    placeholder=""
                    required={true}
                    register={register}
                    rules={{
                      required: "",
                      maxLength: {
                        value: 10,
                        message: " 10",
                      },
                    }}
                  ></Input>
                </div>
                <div className="mb-3">
                  <Input
                    id="email"
                    labelText="Email"
                    type="email"
                    placeholder="xxx@example.com"
                    required={true}
                    errors={errors}
                    register={register}
                    rules={{
                      required: "Email ",
                      pattern: {
                        value: /^\S+@\S+$/i,
                        message: "Email ",
                      },
                    }}
                  ></Input>
                </div>
                <div className="mb-3">
                  <Input
                    id="tel"
                    labelText=""
                    type="tel"
                    placeholder="0987654321"
                    required={true}
                    errors={errors}
                    register={register}
                    rules={{
                      required: "",
                      minLength: {
                        value: 6,
                        message: " 6 ",
                      },
                      maxLength: {
                        value: 12,
                        message: " 12 ",
                      },
                    }}
                  ></Input>
                </div>
                <div className="mb-3">
                  <Input
                    id="address"
                    labelText=""
                    type="address"
                    placeholder=""
                    errors={errors}
                    register={register}
                    required={true}
                    rules={{
                      required: "",
                    }}
                  ></Input>
                </div>
                <div className="mb-3">
                  <Textarea
                    id="message"
                    labelText=""
                    placeholder=""
                    rows="5"
                    errors={errors}
                    register={register}
                  ></Textarea>
                </div>
              </div>
            </div>
            <div className="mb-5">
              <h2 className="h4 mb-4 text-dark"></h2>
              <div className="row mb-3">
                <div className="col-6 mb-3">
                  <CheckboxRadio
                    id="creditCard"
                    name="payment"
                    type="radio"
                    value="creditCard"
                    labelText=""
                    register={register}
                    errors={errors}
                    rules={{
                      required: "",
                    }}
                  />
                </div>
                <div className="col-6 mb-3">
                  <CheckboxRadio
                    id="bankTransfer"
                    name="payment"
                    type="radio"
                    labelText=""
                    value="bankTransfer"
                    register={register}
                    errors={errors}
                    rules={{
                      required: "",
                    }}
                  />
                </div>
              </div>
              {watchPayment === "creditCard" && (
                <div className="row mb-3">
                  <div className="mb-3">
                    <Input
                      id="cardHolder"
                      labelText=""
                      type="text"
                      placeholder="Apple Wang"
                      required={true}
                      errors={errors}
                      register={register}
                      rules={{
                        required: "",
                      }}
                    />
                  </div>
                  <div className="mb-3">
                    <Input
                      id="creditNum"
                      labelText=""
                      type="number"
                      placeholder="0000 0000 0000 0000"
                      required={true}
                      errors={errors}
                      register={register}
                      rules={{
                        required: "",
                        minLength: {
                          value: 12,
                          message: " 12 ",
                        },
                        maxLength: {
                          value: 12,
                          message: " 12 ",
                        },
                      }}
                    />
                  </div>
                  <div className="col-6 mb-3">
                    <Input
                      id="creditCVV"
                      labelText=""
                      type="number"
                      placeholder="000"
                      required={true}
                      errors={errors}
                      register={register}
                      rules={{
                        required: "",
                        minLength: {
                          value: 3,
                          message: " 3 ",
                        },
                        maxLength: {
                          value: 3,
                          message: " 3 ",
                        },
                      }}
                    />
                  </div>
                  <div className="col-6 mb-3">
                    <Input
                      id="creditExpDate"
                      labelText=""
                      type="text"
                      placeholder="MM/YY"
                      errors={errors}
                      register={register}
                      required={true}
                      rules={{
                        required: "",
                        pattern: {
                          value: /^(0[1-9]|1[0-2])\/[0-9]{2}$/i,
                          message:
                            " MM/YY ",
                        },
                      }}
                    />
                  </div>
                </div>
              )}
              {watchPayment === "bankTransfer" && (
                <div className="row mb-3">
                  <small className="text-dark mb-4">
                    *
                    5 
                  </small>
                  <table className="table table-borderless mx-2 mb-4 bg-light">
                    <tbody>
                      <tr>
                        <th></th>
                        <td>822 </td>
                      </tr>
                      <tr>
                        <th></th>
                        <td>493590245732</td>
                      </tr>
                      <tr>
                        <th></th>
                        <td></td>
                      </tr>
                    </tbody>
                  </table>
                  <div className="col-6">
                    <Input
                      id="bankAccount"
                      labelText=" 5 "
                      type="number"
                      placeholder="00000"
                      required={true}
                      errors={errors}
                      register={register}
                      rules={{
                        required: " 5 ",
                        minLength: {
                          value: 5,
                          message: " 5 ",
                        },
                        maxLength: {
                          value: 5,
                          message: " 5 ",
                        },
                      }}
                    />
                  </div>
                </div>
              )}
            </div>
            <div className="d-flex flex-column-reverse flex-sm-row flex-md-column-reverse flex-lg-row justify-content-between align-items-center w-100 mt-5">
              <Link
                className="col-12 col-sm-4 col-md-12 col-lg-3 btn btn-outline-secondary my-3 my-sm-0 my-md-3 my-lg-5 py-3 rounded-0"
                to="/cart"
              >
                
              </Link>
              <button
                type="submit"
                className="col-12 col-sm-4 col-md-12 col-lg-4 btn btn-dark ms-sm-auto py-3 border-0 rounded-0"
              >
                
              </button>
            </div>
          </form>
          <div className="col px-5 pt-4 pb-4 pb-md-5 bg-light">
            <h3 className="h5 mb-4 text-dark"></h3>
            <div className="mb-5">
              {cartData?.carts?.map((item) => {
                return (
                  <div className="d-flex align-items-center mb-3" key={item.id}>
                    <div className="position-relative me-3 border">
                      <img
                        src={item.product.imageUrl}
                        alt={item.product.title}
                        style={{
                          width: "65px",
                          height: "65px",
                          objectFit: "cover",
                        }}
                      />
                      <span className="position-absolute top-0 start-100 translate-middle badge rounded-pill bg-primary">
                        {item.qty}
                      </span>
                    </div>
                    <p className="mb-0">
                      <small>{item.product.title}</small>
                    </p>
                    <p className="ms-auto mb-0 text-muted">
                      <small>NT$ {formatCurrency(item.total)}</small>
                    </p>
                  </div>
                );
              })}
            </div>
            <div className="row mb-5">
              <div className="col-12">
                <label htmlFor="coupon" className="form-label fw-bold">
                  
                  <br />
                  <small className="fw-normal text-muted">
                    
                  </small>
                </label>
              </div>
              <div className="col-9 col-md-12 col-lg-9">
                <input
                  id="coupon"
                  className="form-control"
                  type="text"
                  value={couponCode}
                  placeholder=""
                  onChange={handleCouponChange}
                />
              </div>
              <div className="col-3 col-md-10 col-lg-3 mx-auto mt-md-3 mt-lg-0 p-0">
                <button
                  type="button"
                  className="btn btn-dark d-block w-100"
                  onClick={handleApplyCoupon}
                >
                  
                </button>
              </div>
              {currentCoupon && (
                <div className="col-12 my-2">
                  <span className="fw-normal text-danger">
                    {currentCoupon}
                  </span>
                </div>
              )}
            </div>
            <div className="mt-5 mb-4 my-md-5">
              <div className="d-flex justify-content-between mb-2">
                <p className="mb-0"></p>
                <p className="mb-0">NT$ {formatCurrency(cartData.total)}</p>
              </div>
              <div className="d-flex justify-content-between mb-2">
                <p className="mb-0"></p>
                <p
                  className={`mb-0 ${
                    cartData.total - discountedTotal && "text-danger"
                  }`}
                >
                  {`- NT$ ${
                    discountedTotal
                      ? formatCurrency(cartData.total - discountedTotal)
                      : 0
                  }`}
                </p>
              </div>
              <div className="d-flex justify-content-between h5 fw-bold text-dark">
                <p className="mb-0"></p>
                <p className="mb-0">
                  NT${" "}
                  {discountedTotal
                    ? formatCurrency(discountedTotal)
                    : formatCurrency(cartData.total)}
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Checkout;
